<template>
	<div class="message is-primary">
		<p class="message-header">
			{{ title }}
			<button
				class="button is-small question is-link is-light"
				aria-label="help"
			>
				<span>
					<font-awesome-icon icon="question" />
				</span>
			</button>
		</p>
		<div class="message-body">
			<div class="field">
				<div class="control">
					<label for="point">Số points trong MT4</label>
					<input
						class="input"
						name="point"
						type="number"
						autocomplete="off"
						v-model.number="point"
						@focus="$event.target.select()"
					/>
				</div>
			</div>
			<div class="field">
				<div class="control">
					<label for="point">Khối lượng</label>
					<input
						class="input"
						name="point"
						type="text"
						autocomplete="off"
						v-model="volume"
						@focus="$event.target.select()"
						@keydown="volumeKeyPressed"
					/>
					<p class="input-note">
						Dùng phím mũi tên lên/xuống để tăng/giảm 0.01.
					</p>
				</div>
			</div>
			<div class="field">
				<div class="control">
					<label for="pair">Cặp tiền tệ</label>
				</div>
				<div class="control has-icons-left">
					<div class="select is-fullwidth">
						<select
							name="pair"
							id="pair"
							v-model="pairIndex"
							class="is-fullwidth"
						>
							<option
								v-for="(pair, index) in pairs"
								:key="pair.index"
								v-bind:value="index"
							>
								{{ pair.name }}
							</option>
						</select>
					</div>
					<span class="icon is-left">
						<font-awesome-icon icon="coins" />
					</span>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	props: ["title", "pairs"],
	data() {
		return {
			message: "Hello",
			point: 1000,
			volume: 0.01,
			pairIndex: 0,
			bufferResult: this.pairs[0].value * 0.01
		}
	},
	methods: {
		calculate() {
			const val = this.pairs[this.pairIndex].value
			this.bufferResult = (this.volume * val * this.point) / 1000
			this.$emit("calculated", this.bufferResult)
		},
		volumeKeyPressed(event) {
			if (event.keyCode == 38) {
				this.volume = (Math.round(this.volume * 100) + 1) / 100
			} else if (event.keyCode == 40) {
				const tmp = Math.round(this.volume * 100)
				if (tmp > 0) {
					this.volume = (tmp - 1) / 100
				}
			}
		},
		pointKeyMonitor(event) {
			if (event.keyCode == 38) {
				this.point += 1
			} else if (event.keyCode == 40) {
				if (this.point > 0) {
					this.point -= 1
				}
			}
		}
	},
	watch: {
		volume(newValue) {
			localStorage.volume = newValue
			this.calculate()
		},
		point(newValue) {
			localStorage.point = newValue
			this.calculate()
		},
		pairIndex(newValue) {
			localStorage.pairIndex = newValue
			this.calculate()
		}
	},
	computed: {},
	mounted: function() {
		if (localStorage.volume) {
			this.volume = localStorage.volume
		}
		if (localStorage.point) {
			this.point = localStorage.point
		}
		if (localStorage.pairIndex) {
			this.pairIndex = localStorage.pairIndex
		}
	}
}
</script>

